import React, { useState } from 'react';
// sub components
import ToolBar from './ToolBar';
import NinjaSquadContainer from './NinjaSquadContainer';
import NinjaCard from './NinjaCard';
import { NinjaInfo } from './util';

import './NinjaProfiles.css';

interface NinjaProfilesProps {
  ninja: NinjaInfo[];
}

/**
 * Top level component
 * Holds the state of filtering options
 * Full set of ninja data is a property
 */
function NinjaProfiles(props: NinjaProfilesProps) {
  // filter options
  const [offices, setOffices] = useState(new Set<string>());
  const [filterName, setFilterName] = useState("");

  const ninjaCardItems = props.ninja
    .filter(ninja => offices.size === 0       || offices.has(ninja.office.toLowerCase()))
    .filter(ninja => filterName.length === 0  || ninja.name.toLowerCase().includes(filterName.toLowerCase()))
    .map(ninja =>
      <NinjaCard  
        key = {ninja.name} 
        name = {ninja.name} 
        imagePortraitUrl = {ninja.imagePortraitUrl}
        office = {ninja.office}
        linkedIn = {ninja.linkedIn}
        gitHub = {ninja.gitHub}
        twitter = {ninja.twitter}
      />
    );

  const officeFilterOptions: Set<string> = new Set();
  props.ninja.forEach(ninja => officeFilterOptions.add(ninja.office.toLowerCase()));

  return (
    <article className="ninja-profiles">
      <header>
        <h1>The fellowship of the tretton37</h1>
        <ToolBar 
          officeFilterOptions={officeFilterOptions} 
          filterName={filterName}
          onOfficeFilterChange={offices => setOffices(new Set(offices))}
          onFilterNameChange={setFilterName}
        />
      </header>
      <NinjaSquadContainer>
        {ninjaCardItems}
      </NinjaSquadContainer>
    </article>
  );
}

export default NinjaProfiles;
