import {getOfficeFlag} from './util';
import './NinjaCard.css';

interface NinjaCardProps {
  name: string;
  office: string;
  imagePortraitUrl?: string,
  linkedIn?: string;
  gitHub?: string;
  twitter?: string;
}

/**
 * Represnts one ninja.
 * Component is pure.
 */
function NinjaCard(props: NinjaCardProps) {
  const linkedInItem = 
    <a href={'https://linkedin.com' + props.linkedIn} target="_blank" rel="noreferrer">
      <i className="fab fa-linkedin" />
    </a>;

  const gitHubItem = 
    <a href={'https://github.com/' + props.gitHub} target="_blank" rel="noreferrer">
      <i className="fab fa-github" />
    </a>;

  const twitterItem = 
    <a href={'https://twitter.com/' + props.twitter} target="_blank" rel="noreferrer">
      <i className="fab fa-twitter" />
    </a>;

  return (
    <article className="ninja-card">
      <img className="portrait" src={props.imagePortraitUrl ? props.imagePortraitUrl : "assets/ninja.png"} alt={props.name} />
      <div className="details">
        <h2>
          <span className="name">{props.name}</span>
          <br />
          <span className="office">
            Office: <span className="emoji-font">{getOfficeFlag(props.office)} </span> 
            {props.office}
          </span>
        </h2>
        <address>
          {props.linkedIn ? linkedInItem : undefined}
          {props.gitHub ? gitHubItem : undefined}
          {props.twitter ? twitterItem : undefined}
        </address> 
      </div>
    </article> 
  );
}

export default NinjaCard;
