import { getOfficeFlag } from './util';
import './ToolBar.css';

interface ToolBarProps {
  officeFilterOptions: Set<string>;
  filterName: string;
  onOfficeFilterChange: (a: Set<string>) => void;
  onFilterNameChange: (name: string) => void;
}

const officesSelected: Set<string> = new Set(); // not really Reacty way. React purists will complain? "just remove the duck" for peer review ;-)

/**
 * Toolbar for filter options
 * Office filter buttons are created dynamically from `officeFilterOptions` property
 */
function ToolBar(props: ToolBarProps) {

  // event delegation. checkbox events are collected at a <form> level
  function onOfficeChange(e: React.FormEvent<HTMLFormElement>) {
    const target = e.target as HTMLInputElement;
    const city = target.getAttribute('name');

    // No pattern matching? still? It's 2021 already. Come on Anders, we know you can do it!
    if (!city) return;

    // isn't it beautiful? yes, pure poetry. but we will be pragmatic.
    // offices[target.checked ? 'add' : 'delete'](city);
    if (target.checked) {
      officesSelected.add(city);
    }
    else {
      officesSelected.delete(city);
    }

    props.onOfficeFilterChange(officesSelected);
  }

  // Component is using the "Checkbox hack" to toggle the state
  const officeButtonItems = Array.from(props.officeFilterOptions)
    .map(office => {
      return (
        <div key={office}>
          <input type="checkbox" name={office} id={office} />
          <label htmlFor={office}>
            <span className="emoji-font">
              {getOfficeFlag(office)}
            </span> 
            {office}
          </label>
        </div>
      );
    });

  return (
    <nav className="toolbar">
      <form onChange={onOfficeChange}>
        <div className="button-bar"> 
          {officeButtonItems}
        </div>
        <input 
          className="name-input" 
          type="text" 
          value={props.filterName} 
          onChange={e => props.onFilterNameChange(e.target.value)}
          placeholder="Ninja name" 
          size={20} 
          maxLength={20}
        />
      </form>
    </nav>
  );
}

export default ToolBar;
