import React, { useState, useEffect } from 'react';
import './NinjaSquadContainer.css';

interface NinjaSquadContainerProps {
  children: React.ReactChild[];
}

/**
 * Visual container
 * Visual items arrangement (grid) and pagination
 */
function NinjaSquadContainer(props: NinjaSquadContainerProps) {
  const NUMBER_PER_PAGE = 40;
  const [itemsShown, setItemsShown] = useState(NUMBER_PER_PAGE);

  // reset pagination if another set of children rendered
  useEffect(() => { 
    setItemsShown(NUMBER_PER_PAGE);
  }, [props.children] );

  return (
    <div className="ninja-squad-container">
      <div className="ninja-squad">
          {props.children.slice(0, itemsShown)}
      </div>
      {itemsShown < props.children.length && (
        <button 
          className="more-button" 
          onClick={_ => setItemsShown(itemsShown + NUMBER_PER_PAGE)}>
            More 
        </button> 
      )}
    </div>
  );
}

export default NinjaSquadContainer;